.feed-card {
  position: relative;
  padding: 55px;
  margin: 20px;
  left: 0;
  bottom: 0;
  border-radius: 5px;
  background-color: rgb(245, 255, 248);
  box-shadow: 0 5px 10px rgba(0,0,0,0.1);
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;

  &:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    box-shadow: 0 10px 15px rgba(0,0,0,0.1);
    opacity: 0;
    -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1)
  }

  &:hover {
    cursor: pointer;
    left: 5px;
    bottom: 5px;
    color: black;

    &:after {
      opacity: 1;
    }
  }

  .offset-header {
    position: relative;
    left: -70px;
    top: -35px;
    background-color: rgb(89, 190, 157);
    padding: 15px;
    box-shadow: 0 3px 5px rgba(0,0,0,0.1);
    border-radius: 3px;
  }

  .tech-items {
    display: inline-block;
    margin: 5px;
    padding: 10px;
    border-radius: 30px;
    border: 1px solid black;
    font-size: 14px;

    &:first-child {
      margin-left: 0;
    }
  }

}